.header {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  min-height: 120px;
  width: 100%;
  padding-top: 20px;

  &__container {
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;

    @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
      padding: 0 32px;
    }
    @media screen and (min-width: $desktop) {
      padding: 0 28px;
    }
  }
}
.header-nav {
  border: none;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
  color: $main-text-color;
  font-size: 32px;
  line-height: 1.32;
  transition: transform $anim-duration $anim-timing-function;

  &:hover,
  &:focus {
    transform: scale(1.02);
  }

  &__icon {
    fill: $second-text-color;
  }
  &__text {
    margin-left: 6px;
    color: $second-text-color;

    &:hover,
    &:focus {
      color: $logo-text-color;
    }
  }
}
.nav-menu {
  display: flex;
  justify-content: center;

  &__item {
    &:not(:last-child) {
      margin-right: 12px;

      @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
        margin-right: 20px;
      }
      @media screen and (min-width: $desktop) {
        margin-right: 40px;
      }
    }
  }

  &__link {
    position: relative;
    font-size: 16px;
    line-height: 1.6;
    color: $main-text-color;

    @media screen and (min-width: $tablet) {
      font-size: 18px;
      line-height: 1.56;
    }
    &:hover,
    &:focus {
      color: $second-text-color;
    }

    &::after {
      position: absolute;
      left: 0;
      display: block;
      content: '';
      width: 100%;
      height: 4px;
      border-radius: 2px;
      background-color: $second-text-color;
      opacity: 0;
      transform: scale(0);
      transition: opacity $anim-duration $anim-timing-function,
        transform $anim-duration $anim-timing-function;
    }
    &:hover::after,
    &:focus::after {
      opacity: 1;
      transform: scaleX(1);
    }
  }
}
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  opacity: 0.8;
  background: $third-text-color;
  z-index: 10;
  @media screen and (min-width: $tablet){
    min-height: 70px;
  }
}
