.blog {
  background: $third-background-section;
  @media screen and (max-width: $tablet - 1px) {
    padding-bottom: 40px;
  }
  @media screen and (min-width: $tablet) {
    display: flex;
    flex-direction: row-reverse;
  }

  &::before {
    display: block;
    content: ' ';
    width: 100%;
    max-width: 670px;
    height: 220px;

    @media screen and (max-width: $tablet - 1px) {
      margin-bottom: 40px;
      background-image: url('../images/home/blog.jpg');
    }

    @media (min-device-pixel-ratio: 2) and (max-width: $tablet - 1px),
      (min-resolution: 192dpi) and (max-width: $tablet - 1px),
      (min-resolution: 2dppx) and (max-width: $tablet - 1px) {
      background-image: url('../images/home/blog@2x.jpg');
    }
    @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
      height: 493px;
      max-width: 368px;
      background-image: url('../images/home/blog.jpg');
    }
    @media (min-device-pixel-ratio: 2) and (min-width: $tablet) and (max-width: $desktop - 1px),
      (min-resolution: 192dpi) and (min-width: $tablet) and (max-width: $desktop - 1px),
      (min-resolution: 2dppx) and (min-width: $tablet) and (max-width: $desktop - 1px) {
      background-image: url('../images/home/blog@2x.jpg');
    }

    @media screen and (min-width: $desktop) {
      height: 460px;
      background-image: url('../images/home/blog.jpg');
    }

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &__container {
    @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
    @media screen and (min-width: $desktop) {
      padding: 80px 28px;
    }
  }
  &__data {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.66;
    margin-bottom: 8px;
    @media screen and (min-width: $tablet) {
      font-size: 20px;
      line-height: 1.65;
    }
  }
  &__title {
    font-weight: 300;
    font-size: 38px;
    line-height: 1.26;
    margin-bottom: 24px;
  }
  &__text {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 24px;
    @media screen and (min-width: $tablet) {
      font-size: 18px;
      line-height: 1.56;
    }
  }
  &__button {
    width: 184px;
    @media screen and (min-width: $tablet) {
      font-size: 18px;
      line-height: 1.56;
      width: 196px;
      height: 50px;
    }

    &:hover,
    &:focus {
      color: $third-background-section;
      background: $main-text-color;
      border: 1px solid $third-background-section;
    }
  }
}
