.button-primary {
  display: inline-block;
  background-color: $second-text-color;
  color: $main-text-color;
  border-radius: 5px;
  border: none;
  font-family: inherit;
  font-size: 16px;
  line-height: 1.6;
  transition: transform $anim-duration $anim-timing-function;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}
.icon {
  margin-right: 5px;
}

.button-secondary {
  display: inline-block;
  height: 47px;
  border: 1px solid #ffffff;
  color: $main-text-color;
  border-radius: 5px;
  padding: 10px 36px;
  background: none;
  font-size: 16px;
  line-height: 1.6;
transition: transform $anim-duration $anim-timing-function;
cursor: pointer;
}
